body {
  flex-direction: column;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  display: flex;
}

header {
  justify-content: space-between;
  align-items: center;
  height: auto;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
  padding: 10px 0;
  display: inline-block;
  position: relative;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li {
  margin-left: 20px;
}

.nav_link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-right: 50px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav_link:hover, .nav_link.is-active {
  color: #00acdc;
}

.nav_link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav_link:first-child {
  margin-left: 0;
}

.nav_link:last-child {
  margin-right: 0;
}

.nav_list .nav_link:nth-child(4):hover {
  color: #00acdc;
}

.stars {
  justify-content: flex-start;
  width: 100%;
  max-width: 250px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-size: cover;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

main {
  margin-top: 50px;
}

.catalog {
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  gap: 46px;
  margin-bottom: 48px;
  display: grid;
}

.card {
  color: #060b35;
  box-sizing: border-box;
  border-radius: 5px;
  width: 200px;
  height: auto;
  padding: 16px;
  transition: transform .3s, box-shadow .3s;
  position: relative;
}

.card:hover {
  transform: scale(1.2);
  box-shadow: 0 4px 8px #0003;
}

.card__title:hover {
  color: #34568b;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__describe {
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__rating__reviews {
  color: #060b35;
  text-align: right;
  width: 400px;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
}

.card__price h2 {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 38px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

@media (width <= 1200px) {
  .catalog {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width <= 768px) {
  .catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 480px) {
  .catalog {
    grid-template-columns: 1fr;
  }
}
